<template>
  <div class="hello">
    <ul class="list-group">
      <li class="list-group-item" v-for="(list, index) in lists" :key="index">
      {{ list.text }}<a href="#" class="float-end" v-on:click="deleteList(index)"><i class="bi bi-x"></i></a>
      </li>
    </ul>
    <form>
    <div class="row mt-4">
      <div class="col-10">
        <div class="form-floating">
          <textarea class="form-control" placeholder="Leave a comment here" id="floatingTextarea" v-model="listText"></textarea>
          <label for="floatingTextarea">Comments</label>
        </div>
      </div>
      <div class="col-2">
        <button type="button" class="btn btn-primary col-12 h-100" v-on:click="addList()">Primary</button>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-2">
        <button type="button" class="btn btn-warning col-12 h-100" v-on:click="resetList()">Destroy</button>
      </div>
    </div>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function () {
    var lists;
    let listsz = JSON.parse(localStorage.getItem('lists'));
    if(listsz == null){
      lists = [
          {text: 'Default List 1'},
          {text: 'Default List 2'}
        ];
      localStorage.setItem('lists',JSON.stringify(lists));
    }
    return {
      listText: '',
      lists: listsz
    }
  },
   methods: {
		addList: function() {
			var newList = this.listText.trim();
			if (!newList) {return;}
			this.lists.push(
				{text: newList}
			);
      localStorage.setItem('lists',JSON.stringify(this.lists));
			this.listText = '';
		},
    resetList: function(){
      localStorage.removeItem('lists');
      this.lists = [
          {text: 'Default List 1'},
          {text: 'Default List 2'}
        ];
      localStorage.setItem('lists',JSON.stringify(this.lists));
    },
    deleteList: function(a){
      console.log(a);
      this.lists.splice(a, 1);
      console.log(lists);
      localStorage.setItem('lists',JSON.stringify(this.lists));
    }
   }
}
</script>

